*, *:before, *:after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1300px;
    margin: 0 auto;
}

.header {
    .container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .logo {
            margin-right: 120px;
        }

        .svoy_master_info {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-right: -30px;


            .info {
                margin-right: 80px;
                display: flex;
                flex-direction: row;
                align-items: center;

                img {
                    margin-right: 15px;
                }

                a {
                    text-decoration: none;
                    font-family: 'Panton', sans-serif;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 136%;
                    letter-spacing: 0.055em;
                    color: #3e3e3e;
                }

            }

        }


        button {
            display: block;
            font-family: 'Panton', sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 136%;
            letter-spacing: 0.055em;
            color: #3e3e3e;
            background: linear-gradient(79.2deg, #ffd347 66.1%, #ffdb69 95.75%);
            box-shadow: 0px 4px 1px #ad9547;
            border-radius: 33px;
            border: none;
            padding: 10px 25px;
        }

    }
}

.nav {
    background: #ffffff;
    border: 1px solid #ebf1f4;

    .container {

        ul.mainmenu {
            list-style: none;
            margin: 0;
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            a {
                text-decoration: none;
                display: block;
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 136%;
                letter-spacing: 0.055em;
                color: #000000;
                padding: 10px 30px;
                outline: none;

                &:hover, &:focus {
                    background: linear-gradient(79.2deg, #ffd347 66.1%, #ffdb69 95.75%);
                }
            }
        }
    }
}

.main_banner {
    background-image: url(img/main-page-banner.png);
    background-repeat: no-repeat;
    background-position: top center;
    padding: 80px 0 0 0;

    .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, auto);

        .banner_text {
            p {
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 30px;
                line-height: 130%;
                letter-spacing: 0.055em;
                color: #3e3e3e;
            }

            .text_bold {
                font-weight: 600;
            }

            .banner_arrow {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                align-content: center;

                img {
                    margin-right: 5px;
                }

                p.text_arrow {
                    font-family: 'Panton', sans-serif;
                    font-style: italic;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 130%;
                    letter-spacing: 0.055em;
                    color: #06609c;
                }

            }
        }


        .banner_images {
            grid-row: 1 / 2 span;
            grid-column: 2/ 1 span;


            img.abs {
                position: relative;
                top: -45px;
                margin-right: 15px;
            }

        }

        .banner_consult {
            display: flex;
            flex-direction: row;
            justify-content: start;
            align-items: center;

            button {
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: bold;
                font-size: 16px;
                line-height: 136%;
                letter-spacing: 0.055em;
                color: #3e3e3e;
                background: linear-gradient(79.2deg, #ffd347 66.1%, #ffdb69 95.75%);
                box-shadow: 0px 4px 1px #ad9547;
                border-radius: 33px;
                padding: 10px 20px;
                border: none;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;

                img {
                    margin-left: 10px;
                }
            }

            p {
                font-family: 'Panton', sans-serif;
                font-style: italic;
                font-weight: 400;
                font-size: 14px;
                line-height: 130%;
                letter-spacing: 0.055em;
                color: #777777;
                margin-left: 15px;
            }
        }
    }
}

.nav_bar_content {
    .container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .item {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: start;

            img {
                margin-right: 15px;
            }

            p {
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 136%;
                letter-spacing: 0.055em;
                color: #3e3e3e;
            }
        }
    }
}

.uslugi {
    background: #f3f3f3;
    margin-top: 100px;

    .container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 40px 0;

        .uslugi_text {
            margin-right: 70px;

            h2 {
                margin: 0;
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: bold;
                font-size: 25px;
                line-height: 136%;
                letter-spacing: 0.055em;
                color: #3e3e3e;
            }

            h4 {
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 136%;
                letter-spacing: 0.055em;
                color: #3e3e3e;
                margin: 10px 0;
            }

            p {
                font-family: 'Panton', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 136%;
                letter-spacing: 0.055em;
                color: #3e3e3e;
                margin: 0;
            }
        }

        .uslugi_img {
            flex-shrink: 0;
        }
    }
}

.detail {
    .container {
        .detail_list {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            align-content: center;
            margin-top: 30px;

            a.detail_item {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                text-decoration: none;
                background: #f3f3f3;
                border-radius: 4px;
                margin-right: 35px;
                padding: 10px 30px;

                &:hover, &:focus {
                    box-shadow: 0 0 12px rgba(0, 0, 0, .15);

                    .detail_text {

                        h2 {
                            color: #131313;
                        }

                        p {
                            color: #131313;

                            img {
                                transform: translateX(10px);
                            }
                        }
                    }
                }

                .detail_text {

                    h2 {
                        font-family: 'Panton', sans-serif;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 24px;
                        line-height: 136%;
                        letter-spacing: 0.055em;
                        color: #3e3e3e;
                    }

                    p {
                        font-family: 'Panton', sans-serif;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 136%;
                        letter-spacing: 0.055em;
                        color: #3e3e3e;
                        display: flex;
                        align-items: center;
                    }
                }

                .detail_img {
                    background: rgba(255, 211, 71, 0.45);
                    border-radius: 50%;
                    width: 226px;
                    height: 226px;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    flex-shrink: 0;
                }
            }
        }
    }
}

.calcul{
    margin-top: 70px;

    .container{

        h2{
            margin: 0;
            font-family: "Panton", sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 28px;
            line-height: 136%;
            letter-spacing: 0.055em;
            color: #3E3E3E;
            margin-bottom: 20px;
        }

        .calcul_stick{
            width: 120px;
            height: 6px;
            background: #FFD347;
        }

        .calcul_grid{
            display: grid;
            grid-template-columns: repeat(2, auto);
            grid-template-rows: repeat(2, auto);

            .calcul_reasons{
                grid-row: 1 / 2 span;
                grid-column: 2/ 1 span;
            }

        }
    }
}
